	module.exports = {"pathContext":{"group":[{"node":{"excerpt":"Source I decided to try a new format of writing a story — a \"digest\" one. In this format, I'll be able to mix subjects and share about things I find interesting. Although I don't plan to follow a strict structure, topics will be divided into sections to make it easy to jump between without having to read it all. Medium Certainly, the trending topic in the context of this blog is the story about…","frontmatter":{"title":"Holidays digest 2016","slug":"holiday-digest-2016","tags":["JavaScript","web development"]}}},{"node":{"excerpt":"Admiring the progress of  Web APIs , I decided to shortly summarize some feelings. They are mostly about a borderline (common-sense) why it is worth knowing what is a web app and why we have to shift our paradigm thinking from building websites to building web apps. For this reason, I include few steps as a tutorial to encourage hands-on experimentation. State of internet Unsurprisingly,  more…","frontmatter":{"title":"Think about apps, not websites","slug":"think-about-apps-not-websites","tags":["web development","mobile","JavaScript","Progressive Web Apps"]}}},{"node":{"excerpt":"stream-adventure  is one of the first  workshopper  node modules which teaches  Streams  in node.js. Working on the challenges myself, I got frustrated when simple things did not work for me, so I ended up on the  github repository  doing a bit of triage during my free time. The project: why & how The Why : stream-adventure is a popular project among developers who want to learn  node.js  and its…","frontmatter":{"title":"Triage stream-adventure issue queue","slug":"stream-adventure-triage","tags":["JavaScript","Open Source"]}}},{"node":{"excerpt":"Planning to make an open data GraphQL server, it was natural to first build a small abstraction wrapper which can be re-used in other projects. Basically, the  odp library  I'm going to talk about is just a simplified client for fetching data from the EU Open Data Portal. Getting the library When you have  node.js , the module can be installed by npm: Library API The API of the module is pretty…","frontmatter":{"title":"Building a node.js client for the EU Open Data Portal","slug":"eu-data-portal-nodejs-client","tags":["JavaScript","nodejs","open data"]}}},{"node":{"excerpt":"\"You see things; and you say, 'Why?' But I dream things that never were; and I say, 'Why not?\" — George Bernard Shaw Drupal 8  will soon celebrate 1 year since its release. One interesting fact about this version of the popular PHP CMS is that the  release cycles  are smaller and feature-focused. This makes everybody happier than previous major versions and I will focus on developers' experience…","frontmatter":{"title":"Drupal 8 on Cloud9 IDE","slug":"drupal8-cloud9-ide","tags":["php","web development","Drupal"]}}},{"node":{"excerpt":"In this article I'll be sharing some of my level-up experience learning how to debug node.js apps. It was not easy at first, but once I got it, I felt empowered that now I can work with any code base. Debugging is useful for solving vague problems in other people's code and it's also useful for learning how something actually works. The article might be particularly useful for those used to having…","frontmatter":{"title":"Debugging node.js in Cloud9 IDE","slug":"debugging-nodejs-in-cloud9-ide","tags":["nodejs","JavaScript","Debugging"]}}},{"node":{"excerpt":"Few months ago I attended a  coding competition . In the end of the event the winners demonstrated one of their tools: the  Cloud9 editor . Then it hit me — I had to experiment with it! Later I used it working on various projects in my free time. Here I'm going to share my experience so far — I'm a happy user :) Cloud9 is great So far, I've found several key points/facts why I consider the editor…","frontmatter":{"title":"Cloud9 IDE","slug":"cloud9-editor","tags":["Cloud","Drupal","Cloud9","Tools"]}}},{"node":{"excerpt":"Introduction Recently, I joined an event on  Drupal  in Hungary — the  Drupalaton . It is one of the most exciting yearly Drupal events in Europe, organized by the local community. It gathers hundreds of contributors who collaborate on Drupal issues, share knowledge and have fun together :) I really enjoyed this year's event since it covered a lot of my favorite topics. (discussed shortly after…","frontmatter":{"title":"Drupalaton 2016","slug":"drupalaton-2016","tags":["Drupal","GraphQL","Progressive Web Apps","JavaScript"]}}},{"node":{"excerpt":"The last event from the series of coding battles organised by  Philos , this time in cooperation with Skygate on a life-saving topic using tools like Google Maps and Firebase. Introduction Denis Omelcenco  from Skygate presented the challenge: make an app to locate the closest defibrillator. The initial code base contained helpful starting materials such as a  JSON file listing the defibrillators…","frontmatter":{"title":"Coding battle with Skygate @MolenGeek","slug":"coding-battle-molengeek","tags":["web development","JavaScript","hack events"]}}},{"node":{"excerpt":"Continuing on the exciting journey of coding battles and events, this time I'm so trilled on what happened that I can't wait to put my hands back to code. So the story will be short and simple, hopefully inspiring for you to try building some cool chat bots and join me in the next coding event. Chat Bots Recently, there's been quite some noise around the topic and many companies have already…","frontmatter":{"title":"Coding battle at Microsoft Innovation Center Brussels","slug":"coding-battle-microsoft-innovation-center","tags":["JavaScript","Bots","hack events"]}}}],"first":false,"last":false,"index":3}}

